<template>
  <div
    class="login"
    :style="{ backgroundImage: 'url(' + webSite.login_background_image + ')' }"
  >
    <div class="login-box">
      <div class="login-content">
        <div class="title">{{ webSite.site_name }}</div>
        <div class="content-box">
          <div class="lc-item">
            <span>账号</span>
            <el-input
              v-model.trim="info.account"
              placeholder="请输入账号"
              :class="[infoFlag.account ? 'input-red' : '']"
            ></el-input>
          </div>
          <div class="lc-item">
            <span>密码</span>
            <el-input
              @keyup.enter.native="loginMock"
              v-model.trim="info.password"
              :type="pwdFlag ? 'text' : 'password'"
              placeholder="请输入密码"
              :class="[infoFlag.password ? 'input-red' : '']"
            >
              <img
                slot="suffix"
                :src="pwdImg"
                alt="pwd"
                class="eye"
                @click="pwdFlag = !pwdFlag"
              />
            </el-input>
          </div>
          <div class="error-tip">
            <span v-for="item in errorTip" :key="item">{{ item }}</span>
          </div>
          <el-button type="primary" class="login-button" @click="loginMock"
            >登录</el-button
          >
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";
import ceye from "@/assets/login/ceye.png";
import oeye from "@/assets/login/oeye.png";
import login_background_image from "@/assets/login.webp";
import system_logo from "@/assets/logo.png";
// import Api from "@/api/common";
// import axios from "axios";

export default {
  name: "Login",
  data() {
    return {
      // 校验信息
      info: {
        account: "",
        password: "",
      },
      // 校验信息状态
      infoFlag: {
        account: false,
        password: false,
      },
      errorTip: [], // 错误提示信息
      pwdFlag: false, // 密码框显示与否
      webSite: {
        site_name: "规范设计",
        login_background_image,
        system_logo,
      },
    };
  },
  mounted() {
    localStorage.clear();
  },
  computed: {
    pwdImg() {
      return this.pwdFlag ? oeye : ceye;
    },
  },
  methods: {
    ...mapActions("USER", ["setUserInfo", "setToken"]),
    // 校验
    checkInfo() {
      this.errorTip = [];
      for (const key in this.infoFlag) {
        this.infoFlag[key] = false;
      }

      if (!this.info.account) {
        this.errorTip.push("请输入账号");
        this.infoFlag.account = true;
        return false;
      }
      if (!this.info.password) {
        this.errorTip.push("请输入密码");
        this.infoFlag.password = true;
        return false;
      }
      return true;
    },

    async loginMock() {
      if (!this.checkInfo()) return;
      const that = this;
      const loading = this.$loading({
        lock: true,
        text: "Loading",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.7)",
      });
      const data = {
        token: "xxxx",
        userInfo: {
          avatar: require("@/assets/black.png"),
          name: "阿米斯特朗",
        },
      };

      const loginCheck = (info) => {
        return info.account == "admin" && info.password == "123456";
      };
      if (loginCheck(this.info)) {
        localStorage.setItem("TOKEN", data.token);
        await that.setUserInfo(data.userInfo);
        await that.setToken(data.token);
        setTimeout(() => {
          loading.close();
          that.$message.success("登录成功");
          // 页面跳转
          that.$router.replace('/homepage');
        }, 2000);
      } else {
        loading.close();
        that.$message.error("账号密码错误");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
$font-color: #292a2b;
$font-18: 2.2vh;
$font-26: 3.6vh;
/deep/ .el-input--small .el-input__inner {
  height: 40px;
  line-height: 40px;
}

/deep/.el-input__inner:focus {
  box-shadow: 0 2px 5px rgba(91, 91, 91, 0.1);
}

/deep/.el-input__inner {
  color: #000000;
}
/deep/.el-input__inner:focus {
  border: 1px solid #f50537;
}

.login {
  width: 100%;
  height: 100vh;
  background-image: url(../../assets/login.webp);
  background-repeat: repeat;
  background-size: auto 100%;
  // background-attachment:fixed;
  display: flex;
  justify-content: flex-end;
}

.login-box {
  position: relative;
  width: 37.5%;
  min-width: 476px;
  height: 100%;
  background-color: #ffffff;
  border-radius: 35px 0 0 0;

  .login-content {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 20;
    width: 80%;
    margin-left: 12.2%;
    padding: 18vh 4vh 0;
    height: 60vh;
    border-radius: 4px;
    box-sizing: border-box;

    .title {
      font-size: $font-26;
      font-weight: 600;
      margin-bottom: 8vh;
    }

    .content-box {
      min-height: 340px;
      color: $font-color;
      border-radius: 4px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;

      .lc-item {
        font-size: $font-18;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 4vh;
        box-sizing: content-box;
        span {
          text-align: left;
          margin-bottom: 2vh;
        }

        .eye {
          width: 21px;
          height: 14px;
          margin-top: 14px;
          margin-right: 6px;
          font-size: 18px;
          cursor: pointer;
        }

        .input-red /deep/ .el-input__inner {
          border-color: #f50537;
        }
      }

      .error-tip {
        height: 20px;
        font-size: 10px;
        color: #f50537;
      }

      .login-button {
        height: 40px;
        font-weight: 500;
        margin-top: 3%;
        font-size: 16px;
        letter-spacing: 6px;
        border: none;
        border-radius: 4px;
        background-color: #252525;
        box-shadow: 0 11px 21px #d6e1ff;
      }
      .login-button:hover {
        opacity: 0.9;
      }
      .login-button:active {
        background-color: var(--color-default);
        opacity: 0.95;
      }
    }
  }
}
</style>
